.app {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 16px 0;
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 1.4em;
    margin: auto 0;
    max-height: 500px;
}

.container-full {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000;
    top: 0;
    left: 0;
}

.container {
    padding: 32px;
}

.app-nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: start;
    width: 100%;
}

button {
    background: transparent;
    padding: 0;
    margin: 0;
    border: 0;
    color: #fff;
    cursor: pointer;
}

button:active {
    opacity: 0;
    -webkit-transition: all .6s ease-in-out;
    -o-transition: all .6s ease-in-out;
    transition: all .6s ease-in-out;
}

button:hover {
    opacity: 0.7;
}

form fieldset {
    border: none;
}

form fieldset label {
    display: block;
    margin-bottom: 5px;
}

form fieldset input {
    display: block;
    width: auto;
    margin-bottom: 20px;
    padding: 8px;
}

form fieldset input::-webkit-input-placeholder {
    color: #ccc;
}

form fieldset input:-ms-input-placeholder {
    color: #ccc;
}

form fieldset input::-ms-input-placeholder {
    color: #ccc;
}

form fieldset input::placeholder {
    color: #ccc;
}

form fieldset button[type="submit"] {
    padding: 8px 16px;
    border: 1px solid #ccc;
}

.fa-lg {
    /* font-size: 2em; */
}



